<template>
    <div class="workoutViewComponent">
        <div class="loader" v-if="loading"></div>
        <div v-else>
            <router-link class="btn-back" :to="{ name: 'ExerciseComponent' }"
                >&laquo; go back</router-link
            >
            <h1>{{ exercise[0].title }}</h1>

            <div class="photo"></div>

            <h4>Focused muscle</h4>
            <p>{{ exercise[0].muscle }}</p>

            <h4>Type</h4>
            <p>{{ types[parseInt(exercise[0].type) - 1] }}</p>

            <h4>Description</h4>
            <p>
                {{
                    exercise[0].description !== ""
                        ? exercise[0].description
                        : "No description"
                }}
            </p>
            <router-link class="btn-back" :to="{ name: 'ExerciseComponent' }"
                >&laquo; go back</router-link
            >
        </div>
    </div>
</template>

<script>
import ExerciseService from "../ExerciseService";

export default {
    name: "WorkoutViewComponent",
    data() {
        return {
            exercise: [],
            loading: true,
            types: ["Intensity &amp; Volume", "Volume", "Duration"],
        };
    },
    async created() {
        try {
            this.exercise = await ExerciseService.searchExercisesById(
                this.$route.params.id
            );
            this.loading = false;
        } catch (err) {
            console.log(err.message);
            this.loading = false;
        }
    },
};
</script>

