import axios from "axios";

const url = "api/exercises/";

class ExerciseService {
    // Get Exercises
    static async getExercises() {
        const res = await axios.get(url);
        return res.data;
    }

    // Search Exercises
    static async searchExercises(data) {
        const res = await axios.get(`${url}search`, {
            params: { searchParam: data },
        });
        return res.data;
    }

    // Search Exercise by ID
    static async searchExercisesById(id) {
        const res = await axios.get(`../${url}${id}`);
        return res.data;
    }

    // Create Exercise
    static insertExercise(data) {
        return axios.post(url, data);
    }

    // Update Exercise
    static updateExercise(data) {
        return axios.put(`${url}${data.id}`, data);
    }

    // Delete Exercise
    static deleteExercise(id) {
        return axios.delete(`${url}${id}`);
    }
}

export default ExerciseService;
