<template>
    <div class="searchComponent">
        <form @submit.prevent="searchExercise">
            <input
                type="text"
                v-model="search"
                placeholder="Search by exercise name"
                @keyup="searchExercise"
            />
        </form>
    </div>
</template>

<script>
export default {
    name: "SearchComponent",
    data() {
        return {
            search: "",
        };
    },
    methods: {
        searchExercise(e) {
            if (e.keyCode === 13) {
                this.$emit("search-exercise", this.search);
            }
        },
    },
};
</script>

<style scoped>
input {
    width: 100%;
    border: 1px solid #ececec;
    border-radius: 4px;
    padding: 10px;
    margin-top: 10px;
}
</style>