<template>
    <div class="exerciseComponent">
        <div class="loader" v-if="loading"></div>
        <div v-else>
            <a class="btn-add" @click="addExercise">{{
                toggleFormComponent
                    ? "&minus; Close Form"
                    : "&plus; Add Exercise"
            }}</a>
            <FormComponent
                v-show="toggleFormComponent"
                @saved-form="savedForm"
                @update-form="updateForm"
                :editRecord="editMode"
            />
            <SearchComponent @search-exercise="searchExercise" />
            <div class="search-value" v-show="searchValue != ''">
                <strong>Search result for:</strong> {{ searchValue }}
            </div>
            <div v-if="exercises.length">
                <div
                    class="exercise"
                    v-for="exercise in exercises"
                    :key="exercise.id"
                >
                    <div class="exercise-content">
                        <h4>{{ exercise.title }}</h4>
                        <p>
                            {{
                                exercise.description
                                    ? exercise.description
                                    : "No description"
                            }}
                        </p>
                    </div>
                    <a class="btn-view" @click="viewExercise(exercise._id)"
                        >&#x1f441;</a
                    >
                    <a class="btn-edit" @click="editExercise(exercise)"
                        >&#9998;</a
                    >
                    <a class="btn-delete" @click="deleteExercise(exercise._id)"
                        >&times;</a
                    >
                </div>
            </div>
            <div class="no-record" v-else>No records found</div>
        </div>
    </div>
</template>

<script>
import ExerciseService from "../ExerciseService";
import FormComponent from "./FormComponent";
import SearchComponent from "./SearchComponent";

export default {
    name: "ExerciseComponent",
    data() {
        return {
            exercises: [],
            loading: false,
            toggleFormComponent: false,
            editMode: {},
            searchValue: "",
        };
    },
    components: {
        FormComponent,
        SearchComponent,
    },
    created() {
        this.getAllExercises();
    },
    methods: {
        async getAllExercises() {
            this.loading = true;
            try {
                this.exercises = await ExerciseService.getExercises();
                this.loading = false;
            } catch (err) {
                console.log(err.message);
                this.loading = false;
            }
        },
        async deleteExercise(id) {
            if (confirm("Are you sure you want to delete this exercise?")) {
                this.loading = true;
                await ExerciseService.deleteExercise(id);
                this.getAllExercises();
            }
        },
        async savedForm(data) {
            this.loading = true;
            this.toggleFormComponent = false;
            await ExerciseService.insertExercise(data);
            this.getAllExercises();
        },
        async updateForm(data) {
            this.loading = true;
            this.toggleFormComponent = false;
            await ExerciseService.updateExercise(data);
            this.getAllExercises();
        },
        async searchExercise(searchVal) {
            this.loading = true;
            try {
                this.exercises = await ExerciseService.searchExercises(
                    searchVal
                );
                this.loading = false;
                this.searchValue = searchVal != "" ? searchVal : "";
            } catch (err) {
                console.log(err.message);
                this.loading = false;
            }
        },
        addExercise() {
            this.toggleFormComponent = !this.toggleFormComponent;

            if (!this.toggleFormComponent) {
                const clearItem = {
                    title: "",
                    type: "",
                    muscle: "",
                    description: "",
                    editMode: false,
                };
                this.editMode = clearItem;
            }
        },
        editExercise(data) {
            const currentItem = {
                _id: data._id,
                title: data.title,
                type: data.type,
                muscle: data.muscle,
                description: data.description,
                editMode: true,
            };
            this.toggleFormComponent = true;
            this.editMode = currentItem;
        },
        viewExercise(id) {
            this.$router.push({ path: `/exercise/${id}` });
        },
    },
};
</script>

