<template>
    <div class="formComponent">
        <form @submit.prevent="submitForm">
            <label for="exercise-name">Exercise name</label>
            <input type="text" id="exercise-name" v-model="exerciseTitle" />
            <small class="error-message" v-show="errors.title">{{
                errors.title
            }}</small>

            <label for="exercise-type">Exercise type</label>
            <select id="exercise-type" v-model="exerciseType">
                <option value="1">Intensity &amp; Volume</option>
                <option value="2">Volume</option>
                <option value="3">Duration</option>
            </select>
            <small class="error-message" v-show="errors.type">{{
                errors.type
            }}</small>

            <label for="exercise-muscle-focus">Muscle focus</label>
            <select id="exercise-muscle-focus" v-model="muscleFocus">
                <optgroup label="Anterior">
                    <option
                        v-for="(muscle, index) in orderedMuscleAnterior"
                        :key="'anterior' + index"
                        :value="muscle"
                    >
                        {{ muscle }}
                    </option>
                </optgroup>

                <optgroup label="Posterior">
                    <option
                        v-for="(muscle, index) in orderedMusclePosterior"
                        :key="'posterior' + index"
                        :value="muscle"
                    >
                        {{ muscle }}
                    </option>
                </optgroup>
            </select>
            <small class="error-message" v-show="errors.muscle">{{
                errors.muscle
            }}</small>

            <label for="exercise-description">Exercise description</label>
            <textarea
                id="exercise-description"
                v-model="exerciseDescription"
            ></textarea>

            <button type="submit" :disabled="btnDisabled">Save</button>
        </form>
    </div>
</template>

<script>
export default {
    name: "FormComponent",
    data() {
        return {
            exerciseId: 0,
            exerciseTitle: "",
            exerciseType: "",
            muscleFocus: "",
            exerciseDescription: "",
            errors: [],
            btnDisabled: false,
            editMode: false,
            muscles: {
                anterior: [
                    "Shoulders",
                    "Chest",
                    "Biceps",
                    "Forearms",
                    "Abs",
                    "Obliques",
                    "Quads",
                    "Abductors",
                    "Adductors",
                ],
                posterior: [
                    "Traps",
                    "Triceps",
                    "Lats",
                    "Lower back",
                    "Glutes",
                    "Hamstrings",
                    "Calves",
                ],
            },
        };
    },
    props: {
        editRecord: Object,
    },
    computed: {
        orderedMuscleAnterior() {
            return Object.values(this.muscles.anterior).sort((a, b) =>
                a > b ? 1 : -1
            );
        },
        orderedMusclePosterior() {
            return Object.values(this.muscles.posterior).sort((a, b) =>
                a > b ? 1 : -1
            );
        },
    },
    methods: {
        async submitForm() {
            this.btnDisabled = true;
            this.errors = [];

            if (this.exerciseTitle === "") {
                this.errors.title = "Exercise name is required.";
            }

            if (this.exerciseType === "") {
                this.errors.type = "Please select an exercise type.";
            }

            if (this.muscleFocus === "") {
                this.errors.muscle = "Please select the muscle focus.";
            }

            if (Object.entries(this.errors).length === 0) {
                const exerciseData = {
                    title: this.exerciseTitle,
                    type: this.exerciseType,
                    muscle: this.muscleFocus,
                    description: this.exerciseDescription,
                };

                if (!this.editMode) {
                    this.$emit("saved-form", exerciseData);
                } else {
                    exerciseData.id = this.exerciseId;
                    this.$emit("update-form", exerciseData);
                }
            } else {
                this.btnDisabled = false;
            }
        },
    },
    watch: {
        editRecord: function (newVal) {
            this.errors = [];

            if (Object.entries(newVal).length > 0) {
                this.exerciseId = newVal._id;
                this.exerciseTitle = newVal.title;
                this.exerciseType = newVal.type;
                this.muscleFocus = newVal.muscle;
                this.exerciseDescription = newVal.description;
                this.editMode = newVal.editMode;
            }
        },
    },
};
</script>

